<template>
    <div class="ion-badge" :class="[modeClass,colorClass]">
        <slot></slot>
    </div>
</template>
<style lang="scss">
    @import "badge.scss";
    @import "badge.ios.scss";
    @import "badge.md.scss";
</style>
<script type="text/javascript">

  /**
   * @component Badge
   * @description
   *
   * ##  / Badge
   *
   * 
   *
   * @props {String} color - 
   * @props {String} [mode='ios'] - 
   *
   * $slot  - 
   *
   * */
  export default{
    name: 'Badge',
    data () {
      return {}
    },
    props: {
      /**
       * colorprimarysecondarydangerlightdark
       * */
      color: [String],
      /**
       * mode  ios/window/android/we/alipay
       * */
      mode: {
        type: String,
        default () { return this.$config.get('mode') || 'ios' }
      }
    },
    computed: {
      // 
      modeClass () {
        return `badge badge-${this.mode}`
      },
      // 
      colorClass () {
        return this.color ? (`badge-${this.mode}-${this.color}`) : ''
      }
    }
  }
</script>



